class stringMethods {
  text: string;

  constructor(text: string) {
    this.text = text;
  }

  splitStringByIndex = () => {
    Array.from(this.text)
      .map(e => (e === e.toUpperCase() ? (e = `%ff%${e.toLowerCase()}`) : e))
      .join("")
      .replace(/%ff%/g, " ");

    return this;
  };

  firstCharUpperCase = () => {
    let str = this.text.toLowerCase();
    const firstCharString = str.substr(0, 1).toUpperCase();
    const restString = str.substr(1);
    this.text = firstCharString + restString;
    return this;
  };

  removeSlash = () => {
    this.text = this.text.replace("/", "");
    return this;
  };

  addStringToEnd = (text: string) => {
    this.text = this.text.concat(` ${text}`);
    return this;
  };
}

export default stringMethods;
